const CHANGE_LANGUAGE = "HEADER/CHANGE_LANGUAGE";
const CHANGE_CURRENT_CITY = "HEADER/CHANGE_CURRENT_CITY";

const initialState = {
    staticContent: {
      textLocation: ":",
      textBasket: "",
      textBasketSum: ".",
      textFindPlaceholder: " ?",
      textCatalogue: " "
    },
    links: {
      linkPhone: "+7 (727)225 43 60",
      linkFavorites: "",
    },
    cities: ["", "", ""],
    currentCity: "",
    favoritesItems: 5,
    basketSum: 7700,
    basketProducts: 4,
    catalogueMenu: [" ", " ", "", "", "", "", "", "", " "],
    linksMenu: [
      {
        title: "",
        link: "/"
      },
      {
        title: " ",
        link: "/about"
      },
      {
        title: "",
        link: "/certificates"
      },
      {
        title: "",
        link: "/recipes"
      },
      {
        title: " ?",
        link: "/buyit"
      },
      {
        title: "",
        link: "/contacts"
      },
    ],
    languages: ["", ""],
    currenLang: ""
}

const HeaderReducer = (state = initialState, action) => {
  switch (action.type) {
    case CHANGE_CURRENT_CITY:
      return {
        ...state,
        currentCity: action.city,
      };
    case CHANGE_LANGUAGE:
      return {
        ...state,
        currenLang: action.lang,
      };
    default:
      return state;
  }
}

export const changeLanguageAC = (lang) => ({type: CHANGE_LANGUAGE, lang});
export const changeCurrentCityAC = (city) => ({type: CHANGE_CURRENT_CITY, city})


export default HeaderReducer;